@import url(https://fonts.googleapis.com/css?family=Roboto);
*{
	margin: 0px;
	padding: 0px;
	box-sizing: border-box;
	font-family: 'Roboto';
	/* list-style: none; */
}

:root {
    --cornellRed: rgb(179,27,27) ;
		--webpageWidth: 960px;
		--halfwebpageWidth: 480px;
		--headerHeight: 13em;
		--coolBlue: rgb(0,132,255);
}

.page{
	position: relative;
	min-height: 99vh;
}

header{
	min-width: 1220px;

}

.navbar-container{

	/* background-color: #6160ff; */
  margin: auto;
  height: 60px;
  position: relative;
  }

.navbar-logo{
	float: left;
  height: 100%;
	/* width: 300px; */
	margin-left: 3em;
	text-align: center;
}


.navbar-logo a{
	font-family: 'Roboto';
	/* color: #eaeaea; */
	color: #6160ff;
	display: block;
	margin: auto;
	text-decoration: none;
	font-size: 1.6em;
	line-height: 60px;
	font-weight: bold;
	-webkit-transition-duration: .35s;
	        transition-duration: .35s;
}


.navbar-logo a:hover{
	font-size: 1.7em;
}

header{
  border-bottom: solid 1.5px #bdbdbd;
}

nav{
  /* background-color: lightblue; */
  float: right;
  height: 100%;
  /* min-width: 60%; */
  position: relative;
  right: 0%;
	/* max-width: 868px; */

}

nav ul{
	/* background-color: yellow; */
  list-style: none;
  height: 80%;
  top: 10%;
  text-decoration: none;
  position: relative;
  display: flex;
	float: right;
}

nav ul li{
	/* background-color: red; */

  height: 100%;
  top: 0%;

}

nav ul li:nth-last-child(1){
	/* margin-right: 160px;  */
}

/* nav ul li:nth-last-child(1):hover{
	/* margin-right: 160px; */


nav ul li a{

	text-decoration: none;
	color: inherit;
	/* line-height: 48px; */


}

nav ul li:hover{
	opacity: 1;

}
#dropdown-content{
	/* transition: ease-in; */
	/* transition-duration: 3s; */
}

#dropdown-content .nav-bar-collapsable-column{
	display: none;
	background-color: white;
	position: absolute;
	top: 41px;
	margin: auto;
	width: 123px;
	/* transition: ease-in; */
	/* transition-duration: 1s; */
}

#dropdown-content:hover .nav-bar-collapsable-column{
	display: flex;
	flex-direction: column;
	/* transition-duration: 2s; */
}

.nav-bar-collapsable-column{
	border: 2px #6160ff solid;

}

.nav-link:hover {
	opacity: 1;
}

.nav-link{
  position: relative;
	text-decoration: none;
	text-decoration: none;
  height: 100%;
  top: 0%;
	margin: auto 30px auto auto;
	padding: 2px 20px 2px 20px;
	text-align: center;
  display: inline-block;
	font-family: 'Roboto';
	color: black;
	line-height: 48px;
	opacity: .6;
	font-weight: 300;
	font-size: 1.5em;
	text-decoration: none;

	/* background-color: red; */
}

.nav-link:after{
  content: '';
  position: absolute;
  z-index: 0;
  width: 0%;
  height: 2px;
	display: inline;
  right: 10%;
	bottom: 5px;
  /* background: #fff; */
  opacity: 1;
  background: #6160ff;
  -webkit-transition-property: width;
  transition-property: width;
  -webkit-transition-duration: .3s;
          transition-duration: .3s;
  -webkit-transition-timing-function: ease-out;
          transition-timing-function: ease-out;
}

.nav-link:hover::after,
.nav-link:focus::after,
.nav-link:active::after{
  width: 80%;
  left: 10%;
}

.social-media-link{
	/* background-color: red; */
	opacity: .6;
	height: 48px;
	width: 48px;
	/* padding-right: 30px; */
	margin-right: 30px;
	border-radius: 5px;
	overflow: hidden;
}

.social-media-link a img{
	height: 100%;
}

.page-content{
	width: 960px;
	margin: 0px auto 0px auto;
	position: relative;
	min-height: calc(100vh - 62px);
	/* min-height: inherit; */
	/* background-color: lightgray; */
	/* border-left: 2px solid black; */
	/* border-right: 2px solid black; */
	font-family: Arial;
	align-content: center;
}

#landing-page-container{
	display: flex;
	min-height: calc(100vh - 62px - 79px - 79px - 100px);
	padding-bottom: 10%;
	padding-top: 5%;
	/* float: left; */
}

#landing-page-container h1{
	width: 100%;
	display: block;
}

.page-content h1{
	font-size: 2.7em;
	text-align: center;
	padding-top: 30px;
}

.portrait-container{
	height: 100%;
	width: 40%;
	/* background-color: lightgray; */
	display: flex;
	justify-content: center;
	flex-direction: column;
	margin: auto;
}
.circular-portrait{
	width: 336px;
	height: 336px;
	margin: auto auto auto auto;
	overflow: hidden;
	border-radius: 50%;
	position: relative;
	-webkit-transition-duration: .4s;
	        transition-duration: .4s;
	border: 3px solid #6160ff;
}

#dummy-portrait-padding{
	width: 100%;
	height: 20%;
}


.circular-portrait img{
	height: 100%;
	right: 29%;
	position: relative;
}

.circular-portrait:hover{
	-webkit-transform: scale(1.08);
	        transform: scale(1.08);
	/* top: 15px; */
	border-width: thick;
}

.aboutme-container{
	width: 60%;
	margin: auto;
	text-align: center;
}

.aboutme-container hr{
	margin-top: 25px;
	margin-bottom: 25px;
	margin-left: auto;
	margin-right: auto;
	width: 70%;
}


.aboutme-container ul{
	list-style: circle;
	list-style-position: inside;
	margin-bottom: 1em;
}

.aboutme-container h3{
	margin-bottom: 1em;
}

.aboutme-container p{
	line-height: 1.5em;
	margin-bottom: 1em;
}

.resume{
	position: relative;
	width: 802px;
	height: auto;
	margin: auto;
	margin-top: 30px;
	font-family: "PT Sans", Arial;
	border: solid 1.2px black;
	padding: 50px;
	box-sizing: content-box;
	border-radius: 30px;
	box-shadow: 3px 3px;


}
.resume h1{
	padding: 0;
}
.resume a{
	color: inherit;
	text-decoration: none;
}

.resume-header{
	padding-bottom: 10px;
	/* border-bottom: solid gray 1px; */
	text-align: center;
	padding-bottom: 20px;
}
.resume-date{
	float: right;
	font-weight: 520;
}

#coursework{
	padding-top: 8px;
}

.resume-subsection h1{
	display: inline;
	font-size: 1.3em;
	font-weight: 600;
}
.resume-subsection-body{
	margin-left: 30px;
}

.resume-subsection-body-item{
	padding-bottom: 1em;
}
.experience-entry-title{
	font-style: italic;
}


.resume-subsection-body-item ul{

}

.disc-list{
	list-style-position: inside;
	margin-left: 2em;
	width: 80%;
	position: relative;
}

.disc-list ul li{
	list-style: circle;
	margin-left: 1.2em;
}
.position-description{
	font-size: .8em;
}


.arrowed-list{
	list-style: none;
	padding-left: 1em;
}
.arrowed-list li::before{
	content: "→ "
}

.indented{
	padding-left: 2em;
}


















